import React from 'react';
import PropTypes from 'prop-types';
import Titulo from '../../../../components/Titulo';
import habilidades from '../../../../data/skills.json';
import Container from '../../../../components/wrappers/Container';

const ListarHabilidades = ({
    skills = [],
}) => {
    if (skills && skills.length) {
        return (
            <div
                className="container"
            >
                <Container
                    isFluid
                >
                    { skills.map((elemento, indice) => {
                        const classe = indice % 3 === 0 ? ' is-offset-2' : '';
                        return (
                            <div className={`column is-3 ${classe}`} key={elemento.nome}>
                                <h3 className="title is-3">
                                    { elemento.nome || '' }
                                </h3>
                                <ol type="1">
                                    { elemento.skills
                                        ? elemento.skills.map((skill) => (
                                            <li key={skill}>{ skill }</li>
                                        ))
                                        : null }
                                </ol>
                            </div>
                        );
                    })}
                </Container>
            </div>
        );
    }
    return null;
};

ListarHabilidades.propTypes = {
    skills: PropTypes.arrayOf,
};

ListarHabilidades.defaultProps = {
    skills: [],
};

const Skills = () => {
    return (
        <>
            <Titulo
                titulo="Habilidades"
                id="habilidades"
            />
            <ListarHabilidades
                skills={habilidades}
            />
        </>
    );
};

export default Skills;
