/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/no-danger */
import React, { useState } from 'react';
import moment from 'moment';
import PropTypes from 'prop-types';
import Galeria from 'react-image-gallery';
import 'moment/locale/pt-br';
import projetos from '../../../../data/projetos.json';
import './index.css';
import Titulo from '../../../../components/Titulo';
import Hr from '../../../../components/Hr';
import Container from '../../../../components/wrappers/Container';


const ModalGaleria = ({
    elemento = {},
    isOpen = false,
    fecharModal = () => {},
}) => {
    if (isOpen) {
        return (
            <div
                className={`modal modal-${elemento.nome} is-active`}
                style={{
                    display: isOpen ? 'flex' : 'none',
                }}
            >
                <div
                    className="modal-background"
                    onClick={fecharModal}
                />
                <div className="modal-content">
                    <Galeria
                        lazyLoad
                        showNav
                        showThumbnailsshowFullscreenButton
                        showPlayButton={false}
                        items={elemento.imagens}
                    />
                </div>
                <div
                    className="modal-close is-large"
                    aria-label="close"
                    onClick={fecharModal}
                />
            </div>
        );
    }
    return null;
};

ModalGaleria.defaultProps = {
    elemento: [],
    isOpen: false,
    fecharModal: () => {},
};

ModalGaleria.propTypes = {
    elemento: PropTypes.objectOf,
    isOpen: PropTypes.bool,
    fecharModal: PropTypes.func,
};

const ColunaImagem = ({
    elemento,
}) => (
    <div
        className="imagem-principal box has-text-centered"
    >
        { elemento.imagem
            ? (
                <img src={elemento.imagem} alt={elemento.nome} className="responsive-image" />
            )
            : 'Sem imagens' }
    </div>
);

ColunaImagem.defaultProps = {
    elemento: null,
};

ColunaImagem.propTypes = {
    elemento: PropTypes.objectOf,
};

const ColunaInfo = ({
    elemento,
}) => {
    const [isModalOpen, setModalOpen] = useState(false);

    const {
        tipo,
        data,
        deploy,
    } = elemento;
    const descricao = { __html: elemento.descricao };
    const tecnologias = elemento.tecnologias.join(', ');
    const deployFormatado = (deploy === true ? 'Sim' : 'Não');
    const dataFormatada = moment(data, 'DD/MM/YYYY').format('LL');
    return (
        <>
            <ModalGaleria
                elemento={elemento}
                isOpen={isModalOpen}
                fecharModal={() => setModalOpen(!isModalOpen)}
            />
            <h2
                className="title is-2"
            >
                { elemento.nome }
            </h2>
            <h5
                className="title is-5"
            >
                <a href={elemento.url[0]} target="_blank" rel="noopener noreferrer">Link para o projeto</a>
            </h5>
            <p dangerouslySetInnerHTML={descricao} />
            <Hr />
            <ul>
                <li>
                    <strong>Tecnologia:</strong>
                    { ' ' }
                    { tecnologias }
                </li>
                <li>
                    <strong>Tipo:</strong>
                    { ' ' }
                    { tipo }
                </li>
                <li>
                    <strong>Data:</strong>
                    { ' ' }
                    { dataFormatada }
                </li>
                <li>
                    <strong>Deploy:</strong>
                    { ' ' }
                    { deployFormatado }
                </li>
            </ul>
            <br />
            { elemento.imagens
                ? (
                    <div
                        className="button is-dark"
                        onClick={() => setModalOpen(!isModalOpen)}
                    >
                        Ver imagens
                    </div>
                ) : null }
        </>
    );
};

ColunaInfo.defaultProps = {
    elemento: null,
};

ColunaInfo.propTypes = {
    elemento: PropTypes.objectOf,
};

const ListarProjetos = ({
    dados = [],
}) => {
    if (dados) {
        if (dados.length) {
            const corUm = '#DEDEDE';
            const corDois = '#F0EFEF';
            return (
                dados.map((elemento, indice) => {
                    const corEscolhida = indice % 2 === 0 ? corUm : corDois;
                    const corEscolhidaPraClasseUm = corEscolhida === corUm;
                    const classColunaUm1 = corEscolhidaPraClasseUm ? 'column is-3 is-offset-2' : 'column is-5 is-offset-2';
                    const classColunaUm2 = corEscolhidaPraClasseUm ? 'column is-5 is-offset-1' : 'column is-3 is-offset-1';
                    return (
                        <div
                            className="container-fluid projetos"
                            style={{
                                backgroundColor: corEscolhida,
                            }}
                            key={elemento.nome}
                        >
                            <Container
                                isFluid
                            >
                                <div
                                    className={classColunaUm1}
                                >
                                    {
                                        corEscolhidaPraClasseUm
                                            ? <ColunaImagem elemento={elemento} />
                                            : <ColunaInfo elemento={elemento} />
                                    }
                                </div>
                                <div
                                    className={classColunaUm2}
                                >
                                    {
                                        corEscolhidaPraClasseUm
                                            ? <ColunaInfo elemento={elemento} />
                                            : <ColunaImagem elemento={elemento} />
                                    }
                                </div>
                            </Container>
                        </div>
                    );
                })
            );
        }
    }
    return null;
};

ListarProjetos.defaultProps = {
    dados: [],
};

ListarProjetos.propTypes = {
    dados: PropTypes.arrayOf,
};

const Projetos = () => (
    <>
        <Titulo
            titulo="Projetos"
            id="projetos"
        />
        <ListarProjetos
            dados={projetos}
        />
    </>
);

export default Projetos;
