/* eslint-disable react/no-danger */
import React from 'react';
import Titulo from '../../../../components/Titulo';
import experiencias from '../../../../data/experiencias.json';
import Container from '../../../../components/wrappers/Container';
import Hr from '../../../../components/Hr';

const ListarExperiencias = () => (
    <Container>
        <div
            className="column is-offset-2 is-8"
        >
            { experiencias.map((elemento, indice) => {
                const descricao = { __html: elemento.descricao };
                return (
                    <div
                        className="content"
                        key={elemento.empresa}
                    >
                        <h2 className="title is-h2">
                            { elemento.empresa }
                        </h2>
                        <h5 className="subtitle is-h5">
                            <strong>{ elemento.funcao }</strong>
                            ,
                            { ' ' }
                            { elemento.periodo[0] }
                            { ' ' }
                            -
                            { ' ' }
                            { elemento.periodo[1] }
                        </h5>
                        <p dangerouslySetInnerHTML={descricao} />
                        { indice + 1 < experiencias.length
                            ? <Hr />
                            : null }
                    </div>
                );
            })}
        </div>
    </Container>
);

const Experiencia = () => (
    <>
        <Titulo
            titulo="Experiência profissional"
            id="experiencia"
        />
        <ListarExperiencias />
    </>
);

export default Experiencia;
