/* eslint-disable react/no-danger */
import React from 'react';
import Titulo from '../../../../components/Titulo';
import certificados from '../../../../data/certificados.json';
import Container from '../../../../components/wrappers/Container';

const Certificados = () => (
    <>
        <Titulo
            titulo="Certificados"
            id="certificados"
        />
        <Container>
            <div
                className="column is-offset-2"
            >
                <div className="content">
                    <div className="ul">
                        { certificados.map((elemento) => {
                            const descricao = { __html: elemento.descricao };
                            return (
                                <li key={elemento.nome}>
                                    <strong>
                                        {elemento.nome}
                                        :
                                    </strong>
                                    { ' ' }
                                    {elemento.ano}
                                    <br />
                                    <div dangerouslySetInnerHTML={descricao} />
                                </li>
                            );
                        }) }
                    </div>
                </div>
            </div>
        </Container>
    </>
);

export default Certificados;
